/* Fix to make images still underlined */
img {
		vertical-align: baseline; 
	}

b {
		font-weight: bold;
	}

.current {
	font-weight: bold; 
	color: white;
}



.Overlay{
		position: relative;
  		bottom: 12vw; /*Adjust for how far from the bottom of the image the text should be */
  		left: -5px; /*For how far from the left edge the text should be */
    	margin: 0 0 -1em 0; /*Spacing from bottom  */
    	font-size: 6vw;
	}
.OverOverlay{
		position: relative;
  		bottom: -2.5em; /*Adjust for how far from the bottom of the image the text should be */
  		left: 0px; /*For how far from the left edge the text should be */
    	margin: -4em 0 -1em 0; /*Spacing from bottom  */
    	font-weight: bolder;
    	/*max-width: 400px;*/
    	color: black;
	}
	
