// Make Bootstrap stop stretching my legends
// https://stackoverflow.com/questions/16852484/use-fieldset-legend-with-bootstrap
legend {
    width:inherit; /* Or auto */
    padding:0 0.3em 0 0.5em; /* To give a bit of padding on the left and right */
    border-bottom:none;
}